//React
import React from 'react'

//CSS Bootstrap
import {
    Col,
    Grid,
    Image,
    Row
} from "react-bootstrap"

//imgs
import AboutusPic from '../library/AboutusProp.jpg'
import AtlantaBraves from '../library/AtlantaBraves.jpg'
import CraftBeer from '../library/craftBeer.jpeg'


const AboutUs = () =>(
    <div id = "Homebtn">
        <h2 id="aboutUs">About Us</h2>
                <div id="aboutUsContent">
                    <Grid id="aboutUsGrid">
                        <Row className="show-grid">
                            <Col sm ={5}>
                                <Image id="aboutPic" responsive src={AboutusPic} alt="person on the computer"/>
                            </Col>
                            <Col sm ={5}>
                                <p>This is a description of the website. In this paragraph the user will be
                                    informed how and why the website was founded.</p>
                                <p>
                                    <strong>Contact us:</strong>
                                    someemail@somewhere.com</p>
                            </Col>

                        </Row>
                    </Grid>

                </div>
                <div className ="adSide">
                        <Image className= "ad" src={AtlantaBraves} responsive alt="ads"/>
                        <Image className = "ad" src={CraftBeer} responsive alt="ads"/>
                </div>
    </div>
);
export default AboutUs


