import * as actions from "../actions";

const initialState = {
    authenticated: false,
    authenticating: false,
    token: undefined, 
    error: undefined
}

const authentication = (state = initialState, action) => {
    switch(action.type) {
        case actions.LOGOUT:
            return {
                authenticated: false,
                token: undefined
            }
        case actions.BEGIN_AUTHENTICATION:
            return {
                authenticating: true
            }
        case actions.AUTHENTICATION_SUCCESS:
            return {
                authenticating: false,
                authenticated: true,
                token: action.token
            }
        case actions.AUTHENTICATION_FAILURE:
            return {
                authenticating: false,
                error: action.error
            }
        default:
            return state;
    }
}

export default authentication;


