//React
import React from 'react';

//Components
import { Footer } from '../components';

//CSS
import '../css/HomeInfo.css'
import { Button, Form, FormControl,FormGroup, ControlLabel, Carousel } from 'react-bootstrap'
import craftBeer from '../library/craftBeer.jpeg'
import AtlantaBraves from '../library/AtlantaBraves.jpg'

class HomeInfo extends React.Component {
    render() {
        return (
            <div>
                <h1 className ="title">Home details</h1>
                <Carousel >
                <Carousel.Item>
          <img width={900} height={500} alt="900x500" src={craftBeer} />
          <Carousel.Caption>
            <h3>First slide label</h3>
            <p>Picture 1</p>
          </Carousel.Caption>
        </Carousel.Item>
        <Carousel.Item>
          <img align="center" width={900} height={500} alt="900x500" src={AtlantaBraves} />
          <Carousel.Caption>
            <h3>Second slide label</h3>
            <p>Picture 2</p>
          </Carousel.Caption>
        </Carousel.Item>
      </Carousel>
               
                <div id ="homedetails">
                    <Form>
                        <div id ="home-details-left">
                        <table id ="home-details-table">
                            <tr>
                                <td>
                                    <label>Location </label>
                                </td>
                                <td>
                                    <input type ="text" placeholder="City and State"/>
                                </td>
                            </tr>
                            <tr>
                                <td>
                                    <label>Residents </label>
                                </td>
                                <td>   
                                    <input type ="number" placeholder="Number of residents"/>
                                </td>
                            </tr>
                            <tr>
                                <td>
                                    <label>Bathrooms </label>
                                </td>
                                <td>
                                    <input type ="number" placeholder="Number of bathrooms"/>
                                </td>
                            </tr>
                        </table>

                            <FormGroup controlId="formControlsTextarea">
                                <ControlLabel>Description of the home</ControlLabel>
                                <FormControl componentClass="textarea" placeholder="Enter the description of your home" />
                            </FormGroup>
                        </div>  
                        <div id="home-details-right">
                            <input type="number" placeholder="Rent rate per month"/><br/>                            
                            <Button bsStyle="primary">Contact me! </Button>
                        </div>
                    </Form> 
                </div>                                         
                <Footer/>
          </div>
            )        
    }
}

export default HomeInfo;


