//React
import React from "react";

//icons and css
import {ButtonToolbar, ToggleButtonGroup, ToggleButton, Table} from 'react-bootstrap'

const questions = [
    {
        question: "Do you smoke?",
        Response: "yes"
    }, {
        question: "What is your favorite color?",
        Response: "Blue"
    }

];
class Questionnaire extends React.Component {
    render() {
        return (
            <div>
                <Table className="Table" striped bordered condensed hover>
                    <tr className="table-row">
                        <td className="table-header">Questions</td>
                        <td className="table-header">Response</td>
                    </tr>
                    {questions.map(row => (
                        <tr className="table-row">

                            <td className="table-question">{row.question}</td>
                            <td className="table-value">
                                <ButtonToolbar>
                                    <ToggleButtonGroup type="radio" name="options" defaultValue={1}>
                                        <ToggleButton value={1}>Yes</ToggleButton>
                                        <ToggleButton value={2}>No</ToggleButton>
                                    </ToggleButtonGroup>
                                </ButtonToolbar>
                            </td>
                        </tr>
                    ))
}
                </Table>

              

            </div>
        )
    }
}

export default Questionnaire;


