import React, { Component } from 'react';
import { Route, Switch, Redirect } from "react-router-dom";
import { connect } from 'react-redux';
import { NotFound, NavBar } from "./components";
import { Home, HomeInfo, Profile, Matches, OtherProfile, MatchedRoomates, AccountInfo, Terms } from "./containers";
import * as actions from "./actions";

class App extends Component {
    constructor(props) {
        super(props);
        this.privateRoute = this.privateRoute.bind(this);
    }

    privateRoute({ component: Component, ...rest }) {
        return (
            <Route {...rest} render={props => (
                this.props.authenticated
                    ? <Component {...props}/>
                    : <Redirect to='/'/>
            )} />
        )
    }

    render() {
        console.log("props", this.props);
        let PrivateRoute = this.privateRoute;
        return (
            <Switch>
                <Route exact path="/" render={() => (
                    this.props.authenticated ? 
                        <Redirect to="/matches"/> :
                        <Home authenticate={this.props.authenticate}/>
                        
                )}/>
                <NavBar logout={this.props.logout}>
                    <Switch>                        
                        <PrivateRoute path="/matchedRoomates" component={MatchedRoomates}/>
                        <PrivateRoute path="/homeInformation" component={HomeInfo}/>
                        <PrivateRoute path="/profile" component={Profile}/>
                        <PrivateRoute path="/matches" component={Matches}/>
                        <PrivateRoute path="/user/:id" component={OtherProfile}/>
                        <PrivateRoute path="/accountInformation" component={AccountInfo}/>
                        <PrivateRoute path="/terms" component={Terms}/>
                        <PrivateRoute component={NotFound} />                        
                    </Switch>
                    
                </NavBar>
                
            </Switch>
        );
    }
}
const mapStateToProps = state => {
    return {
        token: state.authentication.token,
        authenticated: state.authentication.authenticated,
        authenticating: state.authentication.authenticating,
        error: state.authentication.error
    }
}
const mapDispatchToProps = dispatch => {
    return {
        authenticate: (username, password) => {
            dispatch(actions.authenticate(username, password));
        },
        logout: () => {
            dispatch(actions.logout());
        }
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(App);


