//React
import React from 'react';

//Components
import { Footer, AboutUs, TermsAndConditions } from '../components';
import { Image, Grid, Col } from 'react-bootstrap'
//CSS
import Logo from "../library/Room40_mixedSteveWatson.jpg"
import "../css/home.css"
import "../css/terms.css"
class Terms extends React.Component {
    render() {
        return (
            <div id ="container">
                <div className="adBar">Ad bar </div>
                <div className="logo">                   
                        <Image bsClass="logo" responsive src ={Logo} alt="logo"/>
                </div>
                <AboutUs/>
                <TermsAndConditions/>
                
                <Footer/>
              
            </div>
            )        
    }
}

export default Terms;


