//React
import React from "react";
import { Link } from "react-router-dom";
//icons and css
import {
    Button,
    FormControl,
    FormGroup,
    Form,
    Grid,
    Col,
    ControlLabel,
    Row
} from 'react-bootstrap'

import States from './States'

const UserInfo = ()=>{
        return(
    <div>
        <Form inline>

            <Grid>
                <Row>
                    <FormGroup controlId="formInlineLocation">
                        <Col>

                            <ControlLabel>Prefered City</ControlLabel>{' '}

                        </Col>
                        <Col>
                            <FormControl type="text" placeholder="City"/>

                        </Col>
                    </FormGroup>{' '}
                </Row>
                <Row>
                    <FormGroup controlId="formInlineLocation">
                        <Col>
                            <ControlLabel>Prefered State</ControlLabel>{' '}
                        </Col>
                        <Col>
                            <States/>  
                        </Col>
                    </FormGroup>{' '}
                </Row>
                <Row>
                    <FormGroup controlId="formInlineEmail">
                        <Col>
                            <ControlLabel>Monthly Rent</ControlLabel>{' '}
                        </Col>
                        <Col>
                            <FormControl type="number" min = "0.0" placeholder="Expected amount of Rent"/>
                        </Col>
                    </FormGroup>{' '}
                </Row>
                <Row>
                    <Col>
                        <ControlLabel>Gender</ControlLabel>
                    </Col>
                    <Col>
                        <FormControl componentClass="select" placeholder="select">
                            <option value="male">Male</option>
                            <option value="female">Female</option>
                        </FormControl>             
                    </Col>
                </Row>
                <Row>
                    <FormGroup controlId="formInlineLocation">                    
                        <Col>                            
                            <ControlLabel>Home Owner</ControlLabel>
                        </Col>
                        <Col>
                            <FormControl componentClass="select" placeholder="select">
                                <option value ="yes">Yes</option>
                                <option value ="no">No</option>
                            </FormControl>
                        </Col>
                    </FormGroup>{' '}
                </Row>
                <Row>
                    <FormGroup controlId="formInlineLocation">                    
                        <Col >                            
                            <Button bsStyle=""><Link  to="./homeinformation" >Upload Home Details</Link></Button>
                        </Col>
                    </FormGroup>{' '}
                </Row>
            </Grid>
        </Form>
    </div>
        )
    }

export default UserInfo;


