//React
import React from "react";


//Components
import {AboutUs, NavBar, SignUp, Footer} from "../components";

//CSS Bootstrap
import {
    Button,
    Checkbox,
    Col,
    ControlLabel,
    FormControl,
    Form,
    FormGroup,
    Grid,
    Image,
    Modal,
    Row
} from "react-bootstrap"
import "../css/home.css"

//imgs
import BikeRiders from '../library/BikeRiders.jpeg'
import Logo from "../library/Room40_mixedSteveWatson.jpg"


class Home extends React.Component {
    constructor(props, context) {
        super(props, context);

 

        this.handleShow = this
            .handleShow
            .bind(this);
        this.handleClose = this
            .handleClose
            .bind(this);

        this.state = {
            show: false
        };
    }

    handleClose() {
        this.setState({show: false});
    }

    handleShow() {
        this.setState({show: true});
    }

    render() {
        return (
            <div id="container">
                <NavBar/>
                <div className="adBar">
                    Ad bar
                </div>
             
                <button onClick={() => this.props.authenticate("Daryan", "abc")}>
                    Login
                </button>

                <Grid>
                    <Row>
                        <Col sm={6}>
                            <h3>Login</h3>
                        </Col>
                        <Col sm ={6}>
                            <h3>Welcome</h3>
                        </Col>
                    </Row>
                    <Row className="show-grid">
                        <Col sm={6}>
                            <Form horizontal>
                                <FormGroup controlId="formHorizontalEmail">
                                    <Col componentClass={ControlLabel} sm={2}>
                                        Email
                                    </Col>
                                    <Col sm={5}>
                                        <FormControl type="email" placeholder="Email"/>
                                    </Col>
                                </FormGroup>

                                <FormGroup controlId="formHorizontalPassword">
                                    <Col componentClass={ControlLabel} sm={2}>
                                        Password
                                    </Col>
                                    <Col sm={5}>
                                        <FormControl type="password" placeholder="Password"/>
                                    </Col>
                                </FormGroup>

                                <FormGroup>
                                    <Col smOffset={2} sm={5}>
                                        <Checkbox>Remember me</Checkbox>
                                    </Col>
                                </FormGroup>

                                <FormGroup>
                                    <Col smOffset={2} sm={6}>
                                        <Button type="submit">Sign in</Button>
                                        <Button bsStyle="primary" onClick={this.handleShow}>Sign Up</Button>
                                    </Col>
                                </FormGroup>
                            </Form>
                        </Col>
                        <Col sm={6}>
                            <Image src={Logo} responsive alt="Logo"/>
                        </Col>
                    </Row>
                    <Row></Row>
                </Grid>

                <Modal show={this.state.show} onHide={this.handleClose}>
                    <Modal.Header closeButton>
                        <Modal.Title>Sign Up</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        <SignUp onsubmit = {this.formPreventDefault}/>
                    </Modal.Body>
                    <Modal.Footer>
                        <Button onClick={this.handleClose}>Close</Button>
                    </Modal.Footer>
                </Modal>

                <AboutUs/>
                <Footer/>
            </div>

        );
    }
}

export default Home;


