//React
import React from 'react';

//Components
import { Footer, RoomatesTable } from '../components';



//CSS
import '../css/matching.css';

import data from '../library/tempPeople.json'

class Matches extends React.Component {
    render() {
        return (
            <div>
               
                
                <h1 className ="title">Matches Found</h1>
                <div className="adBar">
                    Ad bar
                </div>
                <RoomatesTable data = {data} />
                <Footer/>
              
            </div>
            )        
    }
}

export default Matches;


