//React
import React from "react";

//components
import { RoomatesTable, Footer } from '../components'
import '../css/matching.css';

import data from '../library/tempPeople.json'

class Events extends React.Component {
    render() {
        return (
            <div>
                <h1 className = "title">Candidate</h1>
                <div className="adBar">
                    Ad bar
                </div>
                <RoomatesTable data= {data}/>
                <Footer/>
            </div>
        )
    }
}

export default Events;


