import React from 'react'
import tempPic from '../library/tempProfile.jpeg'
import { Table } from 'react-bootstrap'
import { Link } from 'react-router-dom'
import '../css/RoomatesTable.css'

export default function RoomatesTable(props){
    return(
        <Table hover stripped="true" condensed>
            <thead>
                <tr>
                    <th></th>
                    <th>Member Name</th>                    
                    <th>Location</th>
                    <th></th>
                    <th></th>
                </tr>
            </thead>
            <tbody>
                
                    {
                        props.data.map(row => (
                            <tr>
                                <td><img  alt ="profile" className ="profilepic" src={tempPic}/></td>
                                <td>{row.name}</td>
                                <td>{row.location}</td>
                                <td>{<Link to="/user/1">{row.more}</Link>}</td>
                                <td>{row.report}</td>
                            </tr>
                        ))
                    }
                
            </tbody>
        </Table>
    );
}


