//React
import React from "react";

//Components
import {Footer, UserInfo} from '../components'

//Icons and images
import tempProfile from '../library/tempProfile.jpeg'

//CSS
import '../css/Profile.css'
import {Image, Form, FormGroup, FormControl, ControlLabel} from 'react-bootstrap'

class Profile extends React.Component {
    render() {
        return (
            <div id="ep-container">
                <div id="ep-left">
                    <Image circle className="ep-profilepic" src={tempProfile} alt="profilepic"/>
                    <UserInfo/>
                </div>
                <div id="ep-right">
                             
                <Form>
                    <FormGroup controlId="formControlsTextarea">
                    <ControlLabel>Description</ControlLabel>
                    <FormControl componentClass="textarea" placeholder="Enter your Description here" />
                    </FormGroup>
                </Form>
                  
  
                </div>
                <Footer/>
            </div>
        )
    }
}

export default Profile;


