//React
import React from 'react'
import axios from 'axios'

import {
    Form,
    FormGroup,
    FormControl,
    Col,
    ControlLabel,
    Checkbox,
    Button
} from "react-bootstrap"
import TermsAndConditions from './TermsAndConditions';

import '../css/TermsAndConditions.css'

class SignUp extends React.Component {
    state ={
        firstName:'',
        lastName:'',
        email:'',
        date:'',
        password:'',
        password2:''
    }
        handleChange = event => {
            this.setState({ name: event.target.value });
          }
        
          handleSubmit = event => {
            event.preventDefault();
        
            const user = {
              name: this.state.firstName,
              lastName:this.state.lastName,
              email: this.state.email,
              date: this.state.date,
              password: this.state.password,
              password2: this.state.password2  
            };
            axios.post("/api/auth/singup",{ user }).then(res => {
                console.log(res);
                console.log(res.data);
            })
    }
    
    render() {

        return (
            <Form horizontal onSubmit= {this.formPreventDefault}>

                <FormGroup controlId="formFirstName2">
                    <Col componentClass={ControlLabel} sm={2}>
                        First Name
                    </Col>
                    <Col sm={5}>
                        <FormControl type="text" placeholder="First Name"/>
                    </Col>
                </FormGroup>

                <FormGroup controlId="formLastName2">
                    <Col componentClass={ControlLabel} sm={2}>
                        Last Name
                    </Col>
                    <Col sm={5}>
                        <FormControl type="text" placeholder="First Name"/>
                    </Col>
                </FormGroup>

                <FormGroup controlId="formHorizontalEmail2">
                    <Col componentClass={ControlLabel} sm={2}>
                        Email
                    </Col>
                    <Col sm={5}>
                        <FormControl type="email" placeholder="Email"/>
                    </Col>
                </FormGroup>

                <FormGroup controlId="Gender">
                    <Col componentClass={ControlLabel} sm={2}>
                        Gender
                    </Col>
                    <Col sm={5}>
                        <FormControl componentClass="select">
                            <option value="male">Male</option>
                            <option value="female">Female</option>
                        </FormControl>

                    </Col>
                </FormGroup>

                <FormGroup>
                <Col componentClass={ControlLabel} sm={2}>
                        Date
                    </Col>
                    <Col sm ={5}>
                       <FormControl type= "text" placeholder="MM/DD/YYYY"/>
                    </Col>
                </FormGroup>

                <FormGroup controlId="formHorizontalPassword2">
                    <Col componentClass={ControlLabel} sm={2}>
                        Password
                    </Col>
                    <Col sm={5}>
                        <FormControl type="password" placeholder="Password"/>
                    </Col>
                </FormGroup>

                <FormGroup controlId="formHorizontalPassword3">
                    <Col componentClass={ControlLabel} sm={2}>
                        Confirm Password
                    </Col>
                    <Col sm={5}>
                        <FormControl type="password" placeholder="Password"/>
                    </Col>
                </FormGroup>
                <div id="terms-scroll">
                    <TermsAndConditions/>
                </div>

                <FormGroup>
                    <div id="agree-terms">
                        <Checkbox id ="terms-read" inline>I agree to the Terms and Conditions to this website</Checkbox>
                    </div>
                </FormGroup>

                <FormGroup>
                    <Col smOffset={2} sm={5}>
                        <Button type="submit">Submit</Button>

                    </Col>
                </FormGroup>
            </Form>
        );
    }
}

export default SignUp


