export const LOGOUT = "LOGOUT";
export const logout = () => {
    return { type: LOGOUT }
}

export const BEGIN_AUTHENTICATION = "BEGIN_AUTHENTICATION";
export const beginAuthentication = () => {
    return { type: BEGIN_AUTHENTICATION }
}

export const AUTHENTICATION_SUCCESS = "AUTHENTICATION_SUCCESS";
export const authenticationSuccess = token => {
    return { type: AUTHENTICATION_SUCCESS, token }
}

export const AUTHENTICATION_FAILURE = "AUTHENTICATION_FAILURE";
export const authenticationFailure = error => {
    return { type: AUTHENTICATION_FAILURE, error }
}

export const authenticate = (username, password) => {
    return dispatch => {
        dispatch(beginAuthentication());
        dispatch(authenticationSuccess("abc123"));
    }
}


