
//React
import React from "react";
import { Link } from "react-router-dom";

//Components
import { Button, Navbar, Nav, NavItem, NavDropdown, MenuItem} from 'react-bootstrap';


//css
import '../css/NavBar.css'

const NavBar = props => {
    return (
        <div id="heading-container">            
        <Navbar  fixedTop  collapseOnSelect className="navbar-banner">
          <Navbar.Header>
            <Navbar.Brand>
              <Link className="navbar-words" to="/">Roommates 40+</Link>
              <Link className="navbar-words" to="/"></Link>
              
            </Navbar.Brand>
          </Navbar.Header>
          <Nav pullRight>
          <NavItem >
            <Link className="navbar-words" to="/matches">Find Roommates</Link>              
          </NavItem>
          
            <NavItem >
              <Link className="navbar-words" to="/matchedRoomates">Candidate</Link>
            </NavItem>
            <NavDropdown   title="Welcome User" id="basic-nav-dropdown"  >
              <MenuItem >
              <Link to="/profile">Edit Profile</Link>
              </MenuItem>
              <MenuItem >
                <Link to="/accountInformation">Account Information</Link>
              </MenuItem>
              <MenuItem >
                <Link to="/terms">About us and TOS</Link>
              </MenuItem>
              <MenuItem divider />
              <MenuItem >
                <Button onClick={() => props.logout()}>Log off</Button>
              </MenuItem>
            </NavDropdown>
          </Nav>
        </Navbar>
        {props.children}
      </div>
        /*<div>
            <div style={{background:"green"}}>
                NavBar
                <ul>
                    <li><Link to="/matches">Matches</Link></li>
                    <li><Link to="/user/1">Other Profile</Link></li>
                    <li><Link to="/profile">Profile</Link></li>
                    <li><Link to="/events">Events</Link></li>
                    <li><Link to="/asdasd">Not Found</Link></li>
                    <li>
                        <button onClick={() => props.logout()}>
                            Logout
                        </button>
                    </li>
                </ul>
 
            </div>
            {props.children}
        </div>*/
  )
}

export default NavBar;


