//React
import React from 'react'
import {Button,Modal, Form, FormGroup, Col, Row, Grid  }  from "react-bootstrap"
//components
import { AccountInfoForm ,Footer } from '../components'

//CSS
import '../css/AccountInfo.css'
import ChangePassword from '../components/ChangePassword';

class AccountInfo extends React.Component{
    constructor(props, context) {
        super(props, context);
    
        this.handleShow = this.handleShow.bind(this);
        this.handleClose = this.handleClose.bind(this);
    
        this.state = {
          show: false
        };
      }
    
      handleClose() {
        this.setState({ show: false });
      }
    
      handleShow() {
        this.setState({ show: true });
      }
      render(){
          return(
    <div id ="ai-container">
       
        <h1 id="ai-title">Account Information</h1>
        <Grid>
            <Row>
                <Col sm = {6}>
                    <h3>Update information</h3>
                </Col>
                <Col sm ={6}>
                    <h3>Change Password</h3>
                </Col>
            </Row>
            <Row>
                <Col sm = {6}>
                    <AccountInfoForm/>
                </Col>
                <Col sm ={6}>
                    <ChangePassword/>
                </Col>
            </Row>
        </Grid>
        <div id = "btnDelete">
            <Button bsStyle ="danger" onClick={this.handleShow}>Remove Account</Button>
        </div>
        



            <Modal show={this.state.show} onHide={this.handleClose}>
          <Modal.Header closeButton>
            <Modal.Title>Remove Account</Modal.Title>
          </Modal.Header>
          <Modal.Body>
          <Form horizontal>

            <FormGroup>
                <Col smOffset={2} sm={12}>
                    Are you sure you want to remove your account?  
                </Col>
            </FormGroup>
             <FormGroup controlId="formHorizontalEmail">
                
                <Col sm={3}/>
                <Col sm={3}>
                    <Button bsStyle="danger" type="submit">Remove</Button>
                </Col>
                <Col sm ={3}>
                    <Button onClick={this.handleClose}>Cancel</Button>
                </Col>
            </FormGroup>
        </Form>
        </Modal.Body>
        </Modal>
        
        <Footer/>
    </div>
);

      }
    }

export default AccountInfo


